@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto",sans-serif;
}
body {
  margin: 0;
  padding: 0;
  background-color: black;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container {
  width: 350px;
  padding: 20px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgb(143, 142, 142),-1px -4px 5px;
  
}
.input-container {
  display: flex;
  width: 100%;
  align-items: center;
  border: 3px solid aqua;
  border-radius: 10px;
  overflow: hidden; 
}
.cityInput {
  flex: 1;
  height: 30px;
  border: none;
  outline: none;
  padding: 10px;
}
.search-icon {
  padding: 10px;
  height: 30px;
  cursor: pointer;
}
.search-icon img {
  object-fit: contain;
  height: 25px;
  width: 25px;
  margin-bottom: 50px;
  margin-top: -5px;
  
}
.images {
  text-align: center;
  height: 150px;
  margin-bottom: 10px;
}
.images img {
  width: 160px;
  height: 160px;
}
.temp,.city,.country {
  margin-top: 5px;
  font-size: 30px;
  text-transform: uppercase;
  color: black;
  text-align: center;
}
.temp {
  font-weight: 500;
}
.city {
  color: rgb(245, 57, 10);
  font-size: 40px;
  font-weight: 400;
}
.country {
  font-size: 20px;
  color: rgb(33, 183, 243);
  font-weight: 500;
}
.cord {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  text-align: center;
  margin-top: 10px;
}
.cord div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.cord div span{
  font-size: 18px;
  font-weight: 450;
}
.humidity-percent,
.wind-speed,
.cord div span:nth-child(2){
  font-size: 20px;
  font-weight: bold;
  padding-top: 5px;
  color: #34d13c;
}
.data-container {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.element {
  text-align: center;

}
.icon {
  height: 50px;
  width: 60px;
}
.text {
  font-size: 15px;
  color: black;
  font-weight: 450;
}
.city-not-found,
.loading-message,
.error-message {
margin-top: 10px;
color: black;
font-size: 30px;
font-weight: 700;
text-align: center;
}
.city-not-found {
  color: red;
}
.error-message{
  color: red;
}
.loading-message{
  color: rgb(6, 190, 6);
}
